import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { WatchlistMenu } from "@rp/react/web/common/composite-components"
import { useModal } from "@rp/react/web/common/providers"
import { Box, IconButton, Typography, styled } from "@rp/react/web/mui/core"
import { ArrowBack, NotInterested } from "@rp/react/web/mui/icons"
import { QueryCheckbox } from "../FilterInput/QueryCheckbox"

type MentionHeaderProps = {
  onClose: () => void
  item: CqsItem
  title?: string
  backButtonTitle: string
  onAddToWatchlist?: (watchlist: Watchlist) => void
  onChange?: (item: CqsItem) => void
  onExclude?: (item: CqsItem) => void
}

const MentionHeaderStyled = styled("div", { name: "MentionHeaderStyled" })(({ theme }) => {
  return {
    alignItems: "center",
    display: "flex",
    color: theme.palette.grey[100],
    justifyContent: "space-between",
    paddingBottom: theme.spacing(1),
    "& svg": {
      width: "20px",
      height: "20px",
    },
  }
})

export const MentionHeader = (props: MentionHeaderProps) => {
  const { onClose, item, backButtonTitle, title, onAddToWatchlist, onChange, onExclude } = props
  const {
    watchlistModal: { handleOpen },
  } = useModal()

  const showWatchlistButton =
    (item.queryType === "entity" || item.queryType === "rp_topic") && onAddToWatchlist !== undefined

  return (
    <MentionHeaderStyled>
      <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
        <RPTooltip title={backButtonTitle} placement="left">
          <IconButton data-testid="back-arrow-button" onClick={onClose} size="small">
            <ArrowBack />
          </IconButton>
        </RPTooltip>
        <Typography variant="body1" fontWeight="bold">
          {title ?? item.name}
        </Typography>
      </Box>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          "& svg": {
            color: "grey.300",
          },
          "& button:hover, & .MuiCheckbox-root:not(.Mui-checked):hover": {
            "& svg": {
              color: "text.primary",
            },
          },
        }}
      >
        {onChange !== undefined ? (
          <QueryCheckbox
            name={item.name}
            queryRelationship={item.queryRelationship}
            onChange={() => onChange(item)}
            placement="top"
          />
        ) : null}
        {onExclude ? (
          <RPTooltip title="Exclude from Search" placement="top">
            <IconButton size="small" onClick={() => onExclude(item)}>
              <NotInterested />
            </IconButton>
          </RPTooltip>
        ) : null}
        {showWatchlistButton ? (
          <WatchlistMenu
            filter={[item]}
            onAddToWatchlist={onAddToWatchlist}
            onHandleOpenModal={handleOpen}
          />
        ) : null}
      </Box>
    </MentionHeaderStyled>
  )
}
